}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}



#nav-container {
  width: 100%;
  /* height: 5vh; */
}

nav {
  padding-top: 1vh;
  padding-bottom: 1vh;
  width: 40%;
  border-bottom: 3px solid black;
  height: 100%;
  margin: auto;
  margin-top: 4vh;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  justify-items: center;
  align-items: center;
}

#projects:hover {
  color: white;
  background-color: #579d1f;
  box-shadow: 2px 2px 2px 1px rgba(0, 0, 0, 0.2);
}

#interests:hover {
  color: white;
  background-color: #0459db;
  box-shadow: 2px 2px 2px 1px rgba(0, 0, 0, 0.2);
}

#home:hover {
  color: white;
  background-color: #e54120;
  box-shadow: 2px 2px 2px 1px rgba(0, 0, 0, 0.2);
}
a {
  color: black;
  text-decoration: none;
}
#projects,
#interests,
#home {
  padding-top: 2vh;
  cursor: pointer;
  border-radius: 5px;
  padding-left: 2vw;
  padding-right: 2vw;
  font-size: large;
  height: 90%;
}

@media only screen and (max-width: 600px) {
  nav {
    width: 80%;
    padding-bottom: 12px;
  }
}

@media only screen and (min-width: 600px) and (max-width: 900px) {
  nav {
    width: 70%;
    padding-bottom: 12px;
  }
}

@media only screen and (min-width: 900px) and (max-width: 1200px) {
  nav {
    width: 70%;
    /* padding-bottom: 12px; */
  }
}

#email-input {
  width: 100%;
  margin: auto;
}
#email {
  margin: auto;
  width: 15%;
  display: block;
  border: 2px solid black;
  border-radius: 5px;
  padding: 5px;
  margin-top: 3vh;
  font-size: small;
  display: grid;
  grid-template-columns: 90% 10%;
}

#copy-icon {
  font-size: medium;
  cursor: pointer;
  justify-self: center;
  position: relative;
  display: inline-block;
}

#copy-icon #copy-text {
  font-size: small;
  visibility: hidden;
  width: 120px;
  background-color: black;
  color: black;
  text-align: center;
  padding: 5px 0;
  border-radius: 6px;
  top: -5px;
  left: 185%;
  position: absolute;
  z-index: 1;
}

#copy-icon:hover {
  color: rgb(180, 178, 178);
}

#copy-icon:hover #copy-text {
  visibility: visible;
  background-color: #fdec08;
}

#copy-icon #copy-text::after {
  content: " ";
  position: absolute;
  top: 50%;
  right: 100%;
  margin-top: -5px;
  border-width: 5px;
  border-style: solid;
  border-color: transparent #fdec08 transparent transparent;
}

@media only screen and (max-width: 600px) {
  #email {
    width: 50%;
  }
  #copy-icon {
    visibility: hidden;
    pointer-events: none;
  }
}

@media only screen and (min-width: 600px) and (max-width: 900px) {
  #email {
    width: 30%;
  }
  #copy-icon {
    visibility: hidden;
    pointer-events: none;
  }
}

@media only screen and (min-width: 900px) and (max-width: 1250px) {
  #email {
    width: 25%;
  }
}

.snackbar {
  visibility: hidden;
  min-width: 250px;
  margin-left: -125px;
  background-color: #593cff;
  color: white;
  text-align: center;
  border-radius: 6px;
  padding: 16px;
  position: fixed;
  z-index: 1;
  left: 50%;
  bottom: 30px;
}

.snackbar.show {
  visibility: visible;
  pointer-events: none;
  -webkit-animation: fadein 0.5s, fadeout 0.5s 2.5s forwards;
          animation: fadein 0.5s, fadeout 0.5s 2.5s forwards;
}

@-webkit-keyframes fadein {
  from {
    bottom: 0;
    opacity: 0;
  }
  to {
    bottom: 30px;
    opacity: 1;
  }
}

@keyframes fadein {
  from {
    bottom: 0;
    opacity: 0;
  }
  to {
    bottom: 30px;
    opacity: 1;
  }
}

@-webkit-keyframes fadeout {
  from {
    bottom: 30px;
    opacity: 1;
  }
  to {
    bottom: 0px;
    opacity: 0;
  }
}

@keyframes fadeout {
  from {
    bottom: 30px;
    opacity: 1;
  }
  to {
    bottom: 0px;
    opacity: 0;
  }
}

