#profile-links {
  width: 40%;
  padding-bottom: 3vh;
  padding-top: 1vh;
  margin: auto;
  display: grid;
  grid-template-columns: 1fr 1fr;
  justify-items: center;
  font-size: x-large;
}

.home-grid {
  display: block;
  height: 100%;
}

.profile-picture {
  display: flex;
  position: relative;
  justify-content: center;
  height: 264px;
  margin: auto;
  width: 198px;
  margin-top: 5vh;
  border: 3px solid black;
  border-radius: 6px;
}

#profile-image {
  display: block;

  width: 100%;
  height: 100%;
}

.overlay {
  position: absolute;
  bottom: 100%;
  left: 0;
  right: 0;
  background-color: wheat;
  opacity: 0.9;
  overflow: hidden;
  width: 100%;
  height: 0;
  transition: 0.5s ease;
}

.profile-picture:hover .overlay {
  bottom: 0;
  height: 100%;
}

.neural-text {
  color: black;
  font-size: 1rem;
  position: absolute;
  top: 50%;
  left: 40%;
  transform: translate(-50%, -50%);
}

.all-projects {
  padding-top: 5vh;
  display: grid;
  grid-template-columns: 1fr 1fr;
  justify-items: center;
  width: 75%;
  margin: auto;
}

h2.project-title {
  font-size: 2vw;
}

p.project-description {
  font-size: 1.2vw;
}

@media only screen and (max-width: 850px) {
  .all-projects {
    padding-top: 5vh;
    display: grid;
    grid-template-columns: 1fr;
    justify-items: center;
    width: 70%;
    margin: auto;
  }
  h2.project-title {
    font-size: 15px;
  }

  p.project-description {
    font-size: 10px;
  }
}

@media only screen and (min-width: 1400px) {
  h2.project-title {
    font-size: x-large;
  }

  p.project-description {
    font-size: medium;
    font-size: initial;
  }

  .all-projects {
    width: 850px;
  }
}

