}

.project-container {
  width: 78%;
  height: 300px;
}

.project-box {
  height: 90%;
  position: relative;
  border-radius: 6px;
  border: 3px solid black;
}

.project-pic {
  display: block;
  height: 100%;
  width: 100%;
}

.project-box:hover {
  border: 3px solid #579d1f;
  box-shadow: 4px 4px 4px 3px rgba(0, 0, 0, 0.2);
}

.project-box:hover .slider {
  bottom: 0;
  height: 100%;
}

.slider {
  position: absolute;
  bottom: 100%;
  left: 0;
  right: 0;
  background-color: #0459db;
  opacity: 0.85;
  overflow: hidden;
  height: 0;
  transition: 0.3s ease;
}

.description {
  color: wheat;
  font-size: 1rem;
  position: absolute;
  top: 50%;
  left: 40%;
  transform: translate(-50%, -50%);
}

@media only screen and (max-width: 750px) {
  .project-container {
    height: 250px;
    width: 90%;
  }
}

